<template>
  <div class="home text-center">

    <v-container>
      <v-row>
      <v-col cols="4"></v-col>
        <v-col cols="4">

        <v-card
          class="mx-auto question_instruction"
          max-width="544"
        >
          <v-card-text>
            <p class="text-h4 text--primary">Welcome to your first Interview</p>
            <p class="text-justify">A quetion dialog with a video camera will pop up after you click on <b>Start</b>, 
            and you are required to give your answer in the 30seconds after which the video camera will stop recording
            and your answer will be saved in the database</p>
            <div class="text--primary">
              Click <b>Start</b> to begin interview.
            </div>
          </v-card-text>
        </v-card><br>

        <v-alert
            v-model="alert"
            color="primary"
            border="left" style="display: none;"
            elevation="2" class="text-h6 text-left question_bg"
            colored-border
            icon="mdi-book"
          >
            {{questions}}
          </v-alert>
          <p class="error" v-if="error">{{error}}</p>
            <v-btn @click="startInterview"  id="startButton" class="primary button" v-if="!questions">Start</v-btn>
            <v-skeleton-loader class="mx-auto" max-width="300" type="card" style="display: none;"></v-skeleton-loader>
            <video id="preview" width="400" height="320" autoplay muted></video>
          <video id="recording" width="400" height="320" controls style="display: none;"></video><br>
          <v-btn fab small depressed class="text-center" id="counter" color="red white--text" style="display: none;" :style="{left: '50%', transform:'translateX(-50%)'}"></v-btn>

          <pre id="log" style="display:none;"></pre>
          <!-- <a id="downloadButton" class="button"> Download </a> -->
        </v-col>
        <v-col cols="4"></v-col>
      </v-row>
    </v-container>
      
  </div>
  
</template>

<script>
import InterviewService from "../InterviewService"

export default {
  name: 'Home',
  data() {
    return {
      questions: '',
      answer: '',
      error: ''
    }
  },
  components: {
  },

  methods: {
    async startInterview() {
      try{
        this.questions = await InterviewService.getQuestions();
      }catch(err) {
        return this.error = err.message;
      }
    },
  },

  mounted() {
      let Script = document.createElement('script')
      Script.setAttribute('src', '/live_video.js')
      document.head.appendChild(Script)
    },
}
</script>
